import {Document} from "~/models/Document";
import {DataJson} from "~/models/json/DataJson";
import {dataJsonSchema} from "~/models/json/dataJsonSchema";

export class Data {
  readonly documents: readonly Document[];

  private constructor(json: DataJson) {
    this.documents = json!.map(documentJson => Document.fromJson(documentJson));
  }

  static fromJson(json: any): Data {
    return new Data(dataJsonSchema.validateSync(json));
  }
}
