import * as yup from "yup";
import {placeJsonSchema} from "~/models/json/placeJsonSchema";
import {documentMetadataJsonSchema} from "~/models/json/documentMetadataJsonSchema";

export const documentJsonSchema = yup.object({
  abstract: yup.string(),
  author: yup.string(),
  contentLocation: placeJsonSchema,
  dateCreated: yup.date(),
  dateModified: yup.date(),
  datePublished: yup.date(),
  encodingFormat: yup.string(),
  id: yup.string().required(),
  keywords: yup.array().of(yup.string().defined()),
  metadata: documentMetadataJsonSchema,
  namedEntitiesByLabel: yup.object(),
  text: yup.string(),
  title: yup.string(),
  url: yup.string().url().required()
});
